import React from 'react';
import './App.css';
import SalesPitch from './salespitch';
import ImageScroller from './ImageScroller';
import FAQ from './FAQ';
import ExecBoard from './ExecBoard';
// import StarterPacks from './StarterPacks';
import WeeklyEntry from './WeeklyEntry';
// import { render } from '@testing-library/react';
import {useState} from 'react';


function App() {
  const [value, setValue] = React.useState(0);

  const handleChange = (newValue:number) => {
    setValue(newValue);
  };

  const renderNewPage = () => {
    switch(value){
      case 0:
        return <SalesPitch />;
      case 1:
        return <WeeklyEntry />;
      case 2:
        return <FAQ />;
      case 3:
        return <ExecBoard />;
      default:
        return <SalesPitch />;
  }};
  
  return (
    <React.Fragment>
          
      <nav>
      <ul>
        <li onClick={() => handleChange(0)}>Home</li>
        <li onClick={() => handleChange(1)}>Picks</li>
        <li onClick={() => handleChange(2)}>FAQ</li>
        <li onClick={() => handleChange(3)}>About Us</li>
      </ul>
    </nav>
      
    <div className="all" style={{ position: "relative" }}>
    <header className="header">
      MUSIC TASTING CLUB
    </header>
    <div className="image-scroller">
    <ImageScroller></ImageScroller>
    </div>
    <h5 className="header2">NORTHWESTERN'S PREMEIR MUSIC DISCOURSE GROUP</h5>
    {renderNewPage()}
    {/* <SalesPitch></SalesPitch>
    <OurMission></OurMission>
    <ThisWeeksPicks></ThisWeeksPicks> */}
    {/* <WeeklyEntry></WeeklyEntry>
    
    <FAQ></FAQ> */}
    
    {/* <ExecBoard></ExecBoard> */}
    {/* <h2 className="check-out-picks">GENRE STARTER PACKS</h2>
    <h5 style={{textAlign:"center"}}>EXPLORE NEW GENRES WITH CLUB PICKS</h5> */}
    {/* <StarterPacks></StarterPacks> */}
    {/* <br></br><br></br> */}
  </div>
 
    </React.Fragment>
  );
}

export default App;
